<template>
  <div class="Profile maxWidthAndCenter">
    <Header />
    <div class="spaceDivider" />

    <div class="headerFlex">
      <h1>Hey, {{ username }}!</h1>

      <button @click="editUser()">
        <img
          src="../assets/iconEdit.png"
          alt=""
          id="btnOpen"
          title="Edit password"
        />
        <img
          src="../assets/iconClose.png"
          alt=""
          id="btnClose"
          class="hide"
          title="Close edit mode"
        />
      </button>
    </div>

    <section class="passwordBox hide" id="passwordBox">
      <section class="profileBox">
        <h2>Info:</h2>

        <dl>
          <dt>Username:</dt>
          <dd>{{ username }}</dd>
        </dl>

        <dl>
          <dt>E-mail:</dt>
          <dd>{{ email }}</dd>
        </dl>
      </section>

      <p class="error" id="error"></p>
      <form @submit.prevent="updatePassword()">
        <label>Old password: </label>
        <input type="password" v-model="oldPassword" />

        <label>New password: </label>
        <input type="password" v-model="newPassword" />

        <label>Repeat new password: </label>
        <input type="password" v-model="repeatPassword" />

        <input type="submit" value="Update" />
      </form>
      <br />
      <hr />
    </section>

    <section>
      <h2>My vibbz</h2>
      <PostLayout
        v-for="post in postsArr"
        :key="post"
        class="deleteBtnStyle"
        :postID="post._id"
        :textEl="post.text"
        :usernameEl="post.username"
        :emotionEl="post.emotion"
        :hashtagsEl="post.hashtags"
        :timestampEL="post.timestamp"
        :showDeleteBtn="true"
      />
    </section>

    <Message
      :theMessage="resultMessage"
      :color="messageColor"
      :reveal="revealStatus"
    />

    <!-- <Footer /> -->
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import Message from "../components/Message.vue";
import PostLayout from "../components/PostLayout.vue";

export default {
  name: "Profile",

  //Import components
  components: {
    Header,
    Footer,
    PostLayout,
    Message,
  },

  data() {
    return {
      username: "",
      email: "",
      postsArr: {},
      oldPassword: "",
      newPassword: "",
      repeatPassword: "",
      resultMessage: "",
      messageColor: "",
      revealStatus: "none",
      errorColor: "#f36a6a",
    };
  },

  methods: {
    getUserInfo() {
      //Get id from sessionStorage
      const userID = sessionStorage.getItem("userID");

      //Fetch to get user info
      fetch(`http://localhost:3000/user/${userID}`, {
        method: "GET",
      }).then((response) => {
        response.json().then((data) => {
          this.username = data.username;
          this.email = data.email;
        });
      });

      //Fetch to get uses posts
      fetch(`http://localhost:3000/posts/${userID}`, {
        method: "GET",
      }).then((response) => {
        response.json().then((data) => {
          this.postsArr = data;
        });
      });
    },

    getImgUrl(emotion) {
      var images = require.context("../assets/moods", false, /\.png$/);
      return images("./" + emotion + ".png");
    },

    editUser() {
      var element = document.getElementById("passwordBox");
      element.classList.toggle("hide");
      var element = document.getElementById("btnClose");
      element.classList.toggle("hide");
      var element = document.getElementById("btnOpen");
      element.classList.toggle("hide");

      this.oldPassword = ""
      this.newPassword = ""
      this.repeatPassword = ""
    },

    updatePassword() {
      document.getElementById("error").innerHTML = "";
      const userID = sessionStorage.getItem("userID");

      if (this.newPassword === this.repeatPassword) {
        const updatePassword = {
          oldPassword: this.oldPassword,
          newPassword: this.newPassword,
        };

        //Fetch to get uses posts
        fetch(`http://localhost:3000/user/${userID}`, {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(updatePassword),
        }).then((response) => {
          response.json().then((data) => {
            if (response.ok) {
              //Console checks
              console.log(data.message);

              //Print message out
              this.resultMessage = "Password is updated";
              this.messageColor = "#c6ddc4";
              this.revealStatus = "block";

              //Remove message after a while
              setTimeout(() => {
                this.resultMessage = "";
                this.messageColor = "white";
                this.revealStatus = "none";
              }, 3000);

              //Hide box again
              var element = document.getElementById("passwordBox");
              element.classList.toggle("hide");
            } else {
              this.resultMessage = data.message;
              this.messageColor = this.errorColor;
              this.revealStatus = "block";

              //Remove message after a while
              setTimeout(() => {
                this.resultMessage = "";
                this.messageColor = "white";
                this.revealStatus = "none";
              }, 3000);
            }
          });
        });
      } else {
        this.resultMessage = "New password dont match";
        this.messageColor = this.errorColor;
        this.revealStatus = "block";

        //Remove message after a while
        setTimeout(() => {
          this.resultMessage = "";
          this.messageColor = "white";
          this.revealStatus = "none";
        }, 3000);
      }
    },
  },

  beforeMount() {
    this.getUserInfo();
  },
};
</script>


<style scoped lang="scss">
.hide {
  display: none;
}

.profileBox {
  position: relative;
}

.headerFlex {
  display: flex;
  justify-content: space-between;
  align-items: center;

  button {
    background-color: white;
    border: none;
    cursor: pointer;

    img {
      width: 35px;
      height: 35px;
    }
  }
}
</style>
