<template>
  <div class="CreateVibbz maxWidthAndCenter">
    <Header />
    <div class="spaceDivider" />

    <h1>Create Vibbz</h1>

    <div class="error" id="error">
      <p v-if="errorArr">{{ errorArr }}</p>
    </div>

    <form @submit.prevent="handleSubmit()">
      <h3>Post a Vibbz</h3>
      <textarea id="post" name="post" v-model="postText" required />

      <!-- <label>Hashtag</label>
      <input type="text" v-model="hashtag" @keyup.space="addHashtag" />
      <div class="hashtag">
        <p
          @click="deleteHashtag(hashtag)"
          v-for="hashtag in hashtagsArr"
          :key="hashtag"
        >
          #{{ hashtag }}
        </p>
      </div>

      <br />
      <br /> -->

          <select v-model="mood">
            <option value="glad">Glad</option>
            <option value="love">Love</option>
            <option value="mad">Mad</option>
            <option value="neutral">Neutral</option>
            <option value="sad">Sad</option>
            <option value="surprised">Surprised</option>
            <option value="tired">Tired</option>
          </select>

          <input type="submit" value="Post Vibbz" />

    </form>

    <!-- <Footer /> -->
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";

export default {
  name: "CreateVibbz",

  data() {
    return {
      postText: "",
      mood: "glad",
      hashtag: "",
      hashtagsArr: [],
      errorArr: "",
    };
  },

  //Import components
  components: {
    Header,
    Footer,
  },

  methods: {
    addHashtag(e) {
      if (this.hashtag) {
        //Check if hashtag already inside array
        if (!this.hashtagsArr.includes(this.hashtag)) {
          //Add to array
          this.hashtagsArr.push(this.hashtag);
        } else {
          document.getElementById("error").innerHTML = "Hashtag already typed";
        }

        //Clear input
        this.hashtag = "";
      }
    },

    deleteHashtag(hashtag) {
      this.hashtagsArr = this.hashtagsArr.filter((item) => {
        return hashtag !== item;
      });
    },

    handleSubmit() {
      console.log("form submit");

      //Validate (not to long text)
      this.errorArr =
        this.postText.length < 244 ? "" : "Post to many characters";

      //Create if so it not sending error forms

      let postData = {
        userID: sessionStorage.getItem("userID"),
        username: sessionStorage.getItem("username"),
        text: this.postText,
        emotion: this.mood,
        hashtags: this.hashtagsArr,
      };

      // console.log(postData)

      fetch("http://localhost:3000/posts", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(postData),
      }).then((response) => {
        response.json().then((data) => {
          console.log(data);

          if (response.ok) {
            window.location.replace("/#/Profile");
          } else {
            console.log(data.message);
          }
        });
      });
    },
  },
};
</script>


<style scoped lang="scss">
form {
  margin: 0 auto;
  padding: 3em 0;
  box-sizing: border-box;

  .formFlex {
    display: flex;
    justify-content: space-between;
  
  }
}
</style>
